<template>
  <div align="center">
    <v-overlay v-model="overlay" z-index="10000">Verifying, please wait...</v-overlay>
    <v-snackbar v-model="alert" top :color="color">
      <div>
        <v-btn @click="alert = false" icon>
          <v-icon>md-close-circle</v-icon>
        </v-btn>
        {{ alert_msg }}
      </div>
    </v-snackbar>
    <div>
      <h1>Face Verification</h1>
    </div>
    <v-card class="pa-2" max-width="640px">
      <div>
        <video id="video" width="100%" height="auto" autoplay></video>
      </div>
      <div align="center">
        <v-btn id="submitbtn" @click="takePhoto" color="rgb(231,113,51)" dark>Capture Face</v-btn>
      </div>
    </v-card>
    <div align="center">
      <canvas id="draw" style="display: none;" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "verify",
  data() {
    return {
      overlay: false,
      headImgSrc: "",
      alert: false,
      alert_msg: "",
      color: ""
    };
  },
  mounted() {
    this.getCompetence();
  },
  methods: {
    tellSize() {
      var box = document.querySelector("video");
      var width1 = box.clientWidth;
      var heigh1 = box.clientHeight;

      // width and height in pixels, including padding and border
      // Corresponds to jQuery outerWidth(), outerHeight()
      var width = box.offsetWidth;
      var height = box.offsetHeight;

      // width and height in pixels, including padding, but without border
      // Corresponds to jQuery innerWidth(), innerHeight()
      alert(
        "width[" +
          width +
          "]height[" +
          height +
          "] client width[" +
          width1 +
          "]client height[" +
          heigh1 +
          "]"
      );
    },
    open(str) {
      this.alert = true;
      this.alert_msg = str;
    },
    getCompetence() {
      // Older browsers might not implement mediaDevices at all, so we set an empty object first
      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {};
      }

      // Some browsers partially implement mediaDevices. We can't just assign an object
      // with getUserMedia as it would overwrite existing properties.
      // Here, we will just add the getUserMedia property if it's missing.
      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function(constraints) {
          // First get ahold of the legacy getUserMedia, if present
          var getUserMedia =
            navigator.webkitGetUserMedia || navigator.mozGetUserMedia;

          // Some browsers just don't implement it - return a rejected promise with an error
          // to keep a consistent interface
          if (!getUserMedia) {
            return Promise.reject(
              new Error("getUserMedia is not implemented in this browser")
            );
          }

          // Otherwise, wrap the call to the old navigator.getUserMedia with a Promise
          return new Promise(function(resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject);
          });
        };
      }

      navigator.mediaDevices
        .getUserMedia({ audio: false, video: true })
        .then(function(stream) {
          var video = document.querySelector("video");
          // Older browsers may not have srcObject
          if ("srcObject" in video) {
            video.srcObject = stream;
          } else {
            // Avoid using this in new browsers, as it is going away.
            video.src = window.URL.createObjectURL(stream);
          }
          // video.style.MozTransform = "scaleX(-1)";
          // video.style.webkitTransform = "scaleX(-1)";
          // video.style.OTransform = "scaleX(-1)";
          // video.style.msFilter = "FlipH";
          // video.style.filter = "FlipH";
          // video.style.transform = "scaleX(-1)";
          video.onloadedmetadata = function(e) {
            console.log(e);
            video.play();
          };
        })
        .catch(function(err) {
          console.log(err.name + ": " + err.message);
        });
    },
    async takePhoto() {
      this.overlay = true;
      let canvas2 = document.getElementById("draw");
      let video = document.getElementById("video");
      var box = document.querySelector("video");
      var width1 = box.clientWidth;
      var heigh1 = box.clientHeight;

      canvas2.width = width1;
      canvas2.height = heigh1;
      let c2 = canvas2.getContext("2d");
      c2.drawImage(video, 0, 0, width1, heigh1);
      //   video.pause();
      var image = canvas2.toDataURL("image/png");
      console.log([image]);
      console.log("click take photo");
      let file = this.dataURLtoFile(image, "taken");
      console.log([file]);
      console.log(file.name + ";" + file.createObjectURL);
      let config = {
        headers: {
          "content-type": "application/octet-stream",
          "Ocp-Apim-Subscription-Key": "ab34ba5577ba40c5bf1df4851fb2ded2"
        },
        timeout: 5000
      };
      axios
        .post(
          "https://australiaeast.api.cognitive.microsoft.com/face/v1.0/detect?returnFaceId=true",
          file,
          config
        )
        .then(response => {
          console.log(response.data);
          if (response.data.length == 0) {
            // alert("No face detected!");
            this.overlay = false;
            this.color = "orange";
            this.open("No face detected!", "Error");
            // location.reload();
          }
          console.log('calling backend')

          this.callBackend(response)
        });
    },
    async callBackend(response) {
      let dataSend = new FormData();
      dataSend.append("username", window.localStorage.getItem("username"));
      // dataSend.append('username','admin')
      dataSend.append("course_id", this.$route.params.id);
      dataSend.append("face_id", response.data[0].faceId);
      let res = await this.$store.dispatch("callAPI", {
        send: dataSend,
        branch: "/verification/",
        call: "DO_NOTHING",
        setData: {}
      });
        this.overlay = false;
      if (res.code == "1") {
        this.$store.commit("SET_SNACKBAR", {
          showing: true,
          text: "Face verification passed!",
          color: "success"
        });

        this.$router.push({path:'/'})
      } else {
        this.$store.commit("SET_SNACKBAR", {
          showing: true,
          text: res.message,
          color: "error"
        });
      }
    },
    dataURLtoFile(dataURL, filename) {
      var arr = dataURL.split(",");
      var mime = arr[0].match(/:(.*?);/)[1];
      var bstr = atob(arr[1]);
      var n = bstr.length;
      var u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, { type: mime });
    }
  },
  closeCameara() {}
};
</script>
<style scoped>
</style>
