<template>
  <v-row width="auto" justify="center">
    <v-dialog v-model="dialog" persistent max-width="600px" max-height="100%">
      <template v-slot:activator="{ on }">
        <v-row>
          <v-col style="padding:0">
            <v-btn text small v-show="isLogin">Welcome, {{usernameL}}</v-btn>
          </v-col>
          <v-col style="padding:0">
            <v-btn v-show="isLogin" outlined rounded dark @click="logout">Log out</v-btn>
          </v-col>
        </v-row>
        <v-btn v-show="!isLogin" outlined rounded dark v-on="on">Log In</v-btn>
      </template>

      <h1 class="white--text">Welcome! Please login</h1>
      <v-card color="rgba(255,255,255,0.75)">
        <v-card-title>
          <span class="headline">Log In</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field v-model="username" label="Username*" required></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field v-model="pass1" label="Password*" type="password" required></v-text-field>
              </v-col>
            </v-row>
          </v-container>
          <small>*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <div></div>
          <v-col>
            <v-row class="pa-2">
              <v-btn dark rounded color="orange" @click="submit" block :loading="loading">Login</v-btn>
            </v-row>
            <v-row justify="center" class="pa-2">
              <v-btn light rounded color="orange" block outlined @click="close">Close</v-btn>
            </v-row>
            <v-row class="pa-2">
              <Signup />
            </v-row>
            <v-row cols="12" class="pa-2">
              <v-btn dark rounded block>Organization Portal</v-btn>
            </v-row>
            <v-row cols="12"></v-row>
          </v-col>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import { mapState } from "vuex";
import Signup from "./Signup";
export default {
  components: {
    Signup
  },
  data() {
    return {
      usernameL:"",
      dialog: false,
      username: "",
      pass1: "",
      loading: null,
      show: null,
      message: ""
    };
  },
  methods: {
    clear() {
      this.username = "";
      this.pass1 = "";
    },
    async logout() {
      let res = await this.$store.dispatch("callAPI", {
        send: {},
        branch: "/logout/",
        call: "DO_NOTHING",
        setData: {}
      });
      console.log(res);
      if (res.code == "ok") {
        this.$store.commit("LOG_OUT", false);
        this.$store.commit("SET_SNACKBAR", {
          showing: true,
          text: "Log out successfully",
          color: "success"
        });
      } else {
        this.$store.commit("SET_SNACKBAR", {
          showing: true,
          text: "Error when logging out",
          color: "error"
        });
      }
    },
    usernameset() {
      this.usernameL = window.localStorage.getItem("username");
    },
    async submit() {
      this.loading = true;
      let send = new FormData();
      send.append("username", this.username);
      send.append("password", this.pass1);
      let branch = "/login/";
      let call = "SET_USER_LOGIN";
      let dataSend = {
        send: send,
        branch: branch,
        call: call,
        setData: this.username
      };
      try {
        let res = await this.$store.dispatch("callAPI", dataSend);
        console.log(res);

        if (res.code == "ok") {
          this.usernameset()
          this.$store.commit("SET_SNACKBAR", {
            showing: true,
            text: "Login successfully",
            color: "success"
          });
          setTimeout(this.$router.push("/"), 3000);
        } else {
          this.$store.commit("SET_SNACKBAR", {
            showing: true,
            text: "Login failed, Invalid login info",
            color: "error"
          });
        }
      } catch {
        this.$store.dispatch("SET_SNACKBAR", {
          showing: true,
          text: "Login failed, Invalid login info",
          color: "error"
        });
      }
      this.loading = false;
      this.close();
    },
    sync() {
      this.dialog = this.dialog_login;
    },
    close() {
      this.$store.state.dialog_login = false;
      this.sync();
    },
    open() {
      this.$store.state.dialog_login = true;
      this.sync();
    }
  },
  computed: {
    ...mapState(["isLogin"]),
  },
  mounted() {
    this.usernameset();
    this.sync();
  },
  created() {
    this.$store.watch(
      state => state.isLogin,
      () => {
        this.isLogin = this.$store.state.isLogin;
      }
    );
  }
};
</script>