<template>
  <v-container>
    <v-card width="100%">
      <v-sheet>
        <v-card>
            <v-card-title>
              My Attendance History
            </v-card-title>
            <v-container>
              <v-data-table
                :headers="headers"
                :items="attendance"
                :sort-by="['course_time']"
                :sort-desc="[false, true]"
                multi-sort
                class="elevation-1"
              ></v-data-table>
            </v-container>
        </v-card>
      </v-sheet>
    </v-card>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      attendance: [],
      headers: [
          {
            text: 'Course name',
            align: 'left',
            sortable: false,
            value: 'course_name',
          },
          { text: 'Date', value: 'course_time' },
          { text: 'QR Pass', value: 'qr_pass' },
          { text: 'Face Pass', value: 'face_pass' },
          { text: 'Survey Pass', value: 'survey_pass' },
        
        ]
    };
  },
  methods: {
    async setAttendance() {
      let send = new FormData();
      send.append("username", window.localStorage.getItem("username"));
      let branch = "/attendance/";
      let call = "DO_NOTHING";
      let setData = {};

      let sendData = {
        send: send,
        branch: branch,
        call: call,
        setData: setData
      };

      let res = await this.$store.dispatch("callAPI", sendData);
      this.attendance = res.attendances;
    }
  },

  computed: {
    // isTrue(vart) {
    //   return vart == "True" ? "No" : "Yes";
    // }
  },
  mounted() {
    this.setAttendance();
  }
};
</script>

