<template>
  <v-container>
    <div id="qrCodingBox">
      <h1>Course QR Code</h1>
      <div id="qrcode" ref="qrcode"></div>
      <!--  -->
      <v-dialog v-model="dialog.showing" width="300">
        <v-card color="rgba(255,255,255,0.75)" style="padding:20px">
          <v-card-text>
            <h2>{{dialog.text}}</h2>
          </v-card-text>
          <v-btn color="orange" large dark @click="dialog.showing = false">OK</v-btn>
          <div class="flex-grow-1" justify="center"></div>
        </v-card>
      </v-dialog>
      <!--  -->
      <!-- <el-button type="primary" @click="goRec">Facial Recognition</el-button> -->
    </div>
  </v-container>
</template>

<script>
import QRCode from "qrcodejs2";
export default {
  name: "qrcoding",
  data() {
    return {
      isScanned: false,
      id: "",
      dialog: {
        showing: true,
        title: "",
        text: "Please show this code to the organisor."
      }
    };
  },
  computed: {},
  mounted() {
    this.qrcode();
    this.acvtiveQRCode();
    this.timer = setInterval(this.queryStatus, 5000);
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.timer);
    next();
  },
  methods: {
    qrcode() {
      let qr = document.getElementById("qrcode");
      let course = this.$route.params.course || this.$route.query.course;
      this.id = course + new Date().getMilliseconds();
      console.log(this.id);
      let qrcode = new QRCode(qr, {
        width: 250,
        height: 250,
        text: `{"course":"${course}",
        "username":"${window.localStorage.getItem("username")}",
          "id": "${this.id}"}`,
        background: "#f0f",
        foreground: "#ff0"
      });
      console.log(qrcode);
    },
    goRec() {
      this.$router.push({
        path: "/user/face",
        query: { course: this.$route.params.course }
      });
    },
    async queryStatus() {
      let data = new FormData();
      data.append("id", this.id);

      let res = await this.$store.dispatch("callAPI", {
        send: data,
        branch: "/check_qr/",
        call: "DO_NOTHING",
        setData: {}
      });
      if (res.code == "scanned") {
        this.$store.commit("SET_SNACKBAR", {
          showing: true,
          text: "Successfully verified QR code",
          color: "success"
        });
        this.goRec();
      }
    },
    async acvtiveQRCode() {
      let data = new FormData();

      let res = await this.$store.dispatch("callAPI", {
        send: data,
        branch: "/active_qr/",
        call: "DO_NOTHING",
        setData: {}
      });
      if (res.code == "true") {
        console.log(res.message);
      }
    }
  },
  created() {}
};
</script>

<style lang="scss" scoped>
#qrCodingBox {
  text-align: center;
}
#qrcode {
  padding-left: calc(50% - 125px);
  padding-top: 50px;
}
</style>
