import Vue from 'vue'
import Vuex from 'vuex'
import API from './service/API'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLogin: false,
    dialog_login: false,
    snackbar: {
      showing: null,
      text: '',
      color: ''
    },
  },
  mutations: {
    SET_SNACKBAR(state, data) {
      state.snackbar.showing = data.showing
      state.snackbar.text = data.text
      state.snackbar.color = data.color
    },
    SET_USER_LOGIN(state, data) {
      state.isLogin = true
      window.localStorage.setItem('username', data)
    },
    DO_NOTHING(state, data) {
      console.log(state)
      console.log(data)
    },
    LOG_OUT(state, data) {
      console.log(state)
      console.log(data)
      window.localStorage.removeItem('username')
      state.isLogin = false
    }
  },
  actions: {
    async callAPI({ commit }, data) {

      try {
        let branch = data.branch
        console.log(branch)

        let send = data.send
        console.log(send)

        let call = data.call
        console.log(call)
        let setData = data.setData
        console.log(setData)
        let res = await API().post(branch, send);
        console.log(res)
        console.log(res.data)
        if (branch == '/authenticate/') {
          if (res.data.code == 'true') {
            this.state.isLogin = true
          }
          else {
            this.state.isLogin = false
          }
        }

        if (res.data.code == "ok") {
          commit(call, setData)
        }
        return res.data
      }
      catch{

        this.state.snackbar = {
          showing: true,
          text: 'Error in calling API',
          color: 'error'
        }
        console.log('Error')
      }
    }
  }
})
