import Vue from 'vue'
import API from './service/API'
// import axios from 'axios';
// import Home from './views/Home.vue'
import User from './views/User.vue'
import store from './store'
import Router from 'vue-router'
import Index from './views/Index.vue'
import GetApp from './views/GetApp.vue'
import Policy from './views/Policy.vue'
import Report from './views/Report.vue'
import Courses from './views/Courses.vue'
import Account from './views/Account.vue'
import History from './views/History.vue'
import TestView from './views/TestView.vue'
import FaceScan from './views/FaceScan.vue'
import AllCourses from './views/AllCourses.vue'
import ManageScan from './views/ManageScan.vue'
import Certificates from './views/Certificates.vue'
import MyCourseView from './views/MyCourseView.vue'
import CourseDetail from './views/CourseDetail.vue'
import ManageCourse from './views/ManageCourse.vue'

// import { async } from 'q'


Vue.use(Router)

export default new Router({
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes: [
    {
      path: '/test',
      name: 'test',
      component: TestView
    }, {
      path: '/getapp',
      name: 'get app',
      component: GetApp
    },
    {
      path: '/policy',
      name: 'policy',
      component: Policy
    },
    {
      path: '/suggest/:id',
      name: 'course detail',
      component: CourseDetail
    },
    {
      path: '/suggest',
      name: 'all courses',
      component: AllCourses
    },
    {
      path:'/qr',
      name:'manage scan QR',
      component:ManageScan
    },
    {
      path: '/manage',
      name: 'manage courses',
      component: ManageCourse
    },
    {
      path: '/',
      name: 'index',
      component: Index
    }, {
      path: '/user',
      name: 'user',
      component: User,
      beforeEnter: async (to, from, next) => {
        let res = await API().post(
          '/authenticate/'
        )
        console.log(res.data)
        if (res.data.code == 'true') {
          // store.commit('SET_LOGIN_STATUS',true)
          next()
        }
        else {
          next('/')
          store.state.dialog_login = true
          store.state.isLogin = false
          window.localStorage.removeItem('username')
          store.state.snackbar = {
            showing: true,
            text: 'Not authenticated!Please Login!',
            color: 'error'
          }
        }
      },

      children: [
        {
          path: 'course/',
          name: 'my course view',
          component: MyCourseView
        },{
          path:'face/',
          name: 'face scan',
          component:FaceScan
        },
        {
          path: 'account',
          name: 'account',
          component: Account
        },
        {
          path: 'suggest',
          name: 'enrollment',
          component: Courses
        },
        {
          path: 'history',
          name: 'history',
          component: History
        }, 
        {
          path: 'report',
          name: 'report',
          component: Report
        }, 
        {
          path: 'certificate',
          name: 'certificates',
          component: Certificates
        }
      ]
    }
  ]
})
